    transition: all 1s;
    width: 5%;
    height: 5%;
    box-sizing: border-box;
    border-radius: 25px;
    padding: 5px;
}

input{
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 42.5px;
    line-height: 30px;
    outline: 0;
    border: 0;
    display: none;
    font-size: 1em;
    border-radius: 20px;
    padding: 0 20px;
    background-color: #07051a;
    color: white;
}

.fa{
    box-sizing: border-box;
    padding: 10px;
    width: 42.5px;
    height: 42.5px;
    position: absolute;
    top: 0;
    right: 0;
    border-radius: 50%;
    color: #07051a;
    text-align: center;
    font-size: 1.2em;
    -webkit-transition: all 1s;
    transition: all 1s;
    text-align: center;
    display: block;
    vertical-align: middle;
}

.searchInput:hover{
    width: 200px;
    cursor: pointer;
}

.searchInput:hover input{
    display: block;
}

.searchInput:hover .fa{
    background: #07051a;
    color: white;
}
